import React from "react";
import { useMemo } from "react";
import { GoogleMap, useLoadScript, Marker } from "@react-google-maps/api";
import logo from "../../images/logo.jpg";
import "./Body.css";
import "bootstrap/dist/css/bootstrap.min.css";
<link
  rel="stylesheet"
  href="https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css"
  integrity="sha384-1BmE4kWBq78iYhFldvKuhfTAU6auU8tT94WrHftjDbrCEXSU1oBoqyl2QvZ6jIW3"
  crossorigin="anonymous"
/>;

function Body() {
  const { isLoaded } = useLoadScript({
    googleMapsApiKey: process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY,
  });

  if (!isLoaded) return <div>Loadding....</div>;
  return <Map />;
}

function Map() {
  const center = useMemo(
    () => ({ lat: 10.760916992642976, lng: 106.66268292632239 }),
    []
  );

  return (
    <div className="body">
      <p class="title">Xe i Thm Nui: </p>
      <div className="middle">
        <p>- Tri Giam Huy Khim Tnh Bnh Thun</p>
        <p>- Tri Giam Tng L Chn Tnh Bnh Phc</p>
        <p>- Tri Giam Phc Ha - M Phc Tnh Tin Giang</p>
      </div>
      <div class="row">
        <div class="col">
          <div class="leftside"></div>
        </div>

        <div class="col">
          <div class="rightside">
            <GoogleMap
              zoom={20}
              center={center}
              mapContainerClassName="map-container"
            >
              <Marker position={center} />
            </GoogleMap>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Body;
